.play {
    height: 100vh;
    width: 100%;
    padding: 0px 31px 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  @media (min-width: 769px) {
    .play {
      max-width: 400px;
      margin: 0 auto;
    }
  }